.main {
    height: 50px;
    border-bottom: 1px solid rgb(206, 206, 206);
    position: fixed;
    width: 100%;
    background-color: white;
    z-index: 2;
    top: 0px;
    left: 0px;
}

.navbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.icon {
    display: flex;
    align-items: center;
    height: 50px;
    margin: 0px 20px;
    position: relative;
    top: 5px;
}

.more-icons {
    display: flex;
    flex-direction: row;
    font-size: 28px;
    gap: 15px;
    margin: 0px 20px;
    position: relative;
    top: 5px;
}

.home::after {
    content: "Home";
    font-size: 12px;
    text-align: center;
    background-color: black;
    color: white;
    padding: 0px 4px;
    border-radius: 3px;
    position: absolute;
    display: none;
    transform: translate(-32px, 30px);
    opacity: 0;
    animation: moremovehome 0.2s ease-in-out 1;
}

.home:hover::after {
    display: inline;
    opacity: 1;
}

@keyframes moremovehome {
    0% {
        transform: translate(-32px, 10px);
        opacity: 0;
    }
    100% {
        transform: translate(-32px, 30px);
        opacity: 1;
    }
}

