@media screen and (max-width: 500px) {
    body {
        overflow-x: hidden;
    }
    .navbar {
        justify-content: space-between;
        display: flex;
    }
    .icon {
        left: 50%;
        transform: translateX(-50%);
    }
    .more-icons {
        display: flex;
        position: fixed;
        top: 93.5%;
        left: 0px;
        height: 6%;
        background-color: white;
        width: 100%;
        margin: 0px;
        justify-content: space-around;
        border-top: 1px solid rgb(206, 206, 206);
    }
    .single {
        top: 5px;
        position: relative;
    }
    .profile::after {
        transform: translate(-36px, -15px);
    }
    @keyframes moremoveprofile {
        0% {
            transform: translate(-36px, 0px);
            opacity: 0;
        }
        100% {
            transform: translate(-36px, -15px);
            opacity: 1;
        }
    }
    .home::after {
        transform: translate(-34px, -15px);
    }
    @keyframes moremovehome {
        0% {
            transform: translate(-34px, 0px);
            opacity: 0;
        }
        100% {
            transform: translate(-34px, -15px);
            opacity: 1;
        }
    }
    .messages::after {
        transform: translate(-44px, -15px);
    }
    @keyframes moremovemes {
        0% {
            transform: translate(-44px, 0px);
            opacity: 0;
        }
        100% {
            transform: translate(-44px, -15px);
            opacity: 1;
        }
    }
    .like::after {
        transform: translate(-36px, -15px);
    }
    @keyframes moremovelike {
        0% {
            transform: translate(-36px, 0px);
            opacity: 0;
        }
        100% {
            transform: translate(-36px, -15px);
            opacity: 1;
        }
    }
    .addpost::after {
        transform: translate(-29px, -15px);
    }
    @keyframes moremovepo {
        0% {
            transform: translate(-29px, 0px);
            opacity: 0;
        }
        100% {
            transform: translate(-29px, -15px);
            opacity: 1;
        }
    }
}

@media screen and (min-width: 1200px) {
    .icon {
        margin-left: 200px;
    }
    .more-icons {
        margin-right: 200px;
    }
}